body,
html {
  margin: 0;
  font-family: 'Roboto', sans-serif; }

body {
  overflow: hidden; }

.hide {
  display: none !important; }

p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0; }

.opening {
  background-color: #1469FF;
  width: 100vw;
  height: 100vh;
  position: relative; }

.star {
  z-index: 10;
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.star__top {
    width: 250px;
    border-top: 130px solid #FF3C82;
    border-left: 80px solid transparent;
    border-right: 80px solid transparent; }

.star__top:before {
      content: '';
      position: absolute;
      top: -60px;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      border-bottom: 70px solid #FF3C82;
      border-left: 40px solid transparent;
      border-right: 40px solid transparent; }

.star__btm {
    width: 250px;
    border-bottom: 130px solid #FF3C82;
    border-left: 80px solid transparent;
    border-right: 80px solid transparent; }

.star__btm:before {
      content: '';
      position: absolute;
      bottom: -60px;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      border-top: 70px solid #FF3C82;
      border-left: 40px solid transparent;
      border-right: 40px solid transparent; }

.circle {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 500px;
  height: 500px;
  z-index: 7;
  border-radius: 50%;
  background-color: #0027C8; }

.rect {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 400px;
  height: 400px;
  z-index: 5;
  background-color: black; }

.text-content {
  z-index: 13;
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white; }

.text-content__title {
    font-size: 64px;
    font-weight: 700; }

.text-content__desc {
    font-size: 24px;
    font-weight: regular; }

.q1 {
  width: 100vw;
  height: 100vh;
  background: #FF3C82;
  position: relative;
  -webkit-transition: 0.5s background;
  transition: 0.5s background; }

.q1 .q-section {
    opacity: 0; }

.q1 .animate-section {
    left: 100%; }

.q-section {
  background: #1469FF;
  height: 100%;
  width: 60%; }

.q-section__wrapper {
    padding: 40px 30px;
    width: 380px; }

.q-section__number {
    font-weight: 700;
    font-size: 64px;
    margin-bottom: 20px;
    color: #FF3C82; }

.q-section__title {
    font-size: 36px;
    color: white;
    font-weight: 700;
    line-height: 48px;
    margin-bottom: 20px; }

.q-section__desc {
    color: #0027C8;
    font-size: 24px;
    line-height: 1.5em;
    margin-bottom: 30px; }

.q-section__options {
    list-style: none;
    padding: 0; }

.q-section__options li {
      margin: 0 0 10px 0;
      padding: 10px 30px;
      border: white solid 2px;
      border-radius: 30px;
      text-align: center;
      cursor: pointer;
      color: white;
      -webkit-transition: all 400ms;
      transition: all 400ms; }

.q-section__options li:hover {
        background: white;
        color: #1469FF; }

.animate-section {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 60%;
  background: #0027C8; }

.animate-section__rect {
    width: 200px;
    height: 200px;
    background: #FF3C82;
    position: absolute;
    top: 100vh;
    left: 50px; }

.animate-section__tri {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 70px 120px 70px;
    border-color: transparent transparent white transparent;
    position: absolute;
    top: calc(100px + 100vh);
    right: -20px; }

.animate-section__circle {
    width: 400px;
    height: 400px;
    border-radius: 50%;
    background: black;
    position: absolute;
    bottom: -900px;
    right: -50px; }

.q2 {
  width: 100vw;
  height: 100vh;
  position: relative;
  background: #0027C8; }

.q2 .q-section {
    position: absolute;
    right: 0;
    background: #0027C8;
    opacity: 0; }

.q2 .q-section__desc {
    color: #1469FF; }

.q2 .animate-section {
    left: 0;
    right: 60%;
    background: #FF3C82;
    overflow: hidden; }

.q2 .animate-section__rect {
      width: 300px;
      height: 300px;
      -webkit-transform: rotate(40deg);
              transform: rotate(40deg);
      background: #1469FF;
      bottom: -400px;
      top: unset;
      left: -170px; }

.q2 .animate-section__tri {
      border-width: 0px 150px 250px 150px;
      top: 120vh;
      left: 100px;
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg); }

.q2 .animate-section__circle {
      width: 200px;
      height: 200px;
      left: 70px;
      bottom: -300px; }

.q3 {
  width: 100vw;
  height: 100vh;
  position: relative;
  background: #FF3C82; }

.q3 .q-section {
    position: absolute;
    right: 0;
    background: #FF3C82;
    opacity: 0; }

.q3 .q-section__number {
    color: white; }

.q3 .q-section__title,
  .q3 .q-section__desc {
    color: black; }

.q3 .animate-section {
    background: #1469FF;
    left: unset; }

.q3 .animate-section__rect {
      width: 230px;
      height: 230px;
      background: #0027C8;
      position: absolute;
      top: 100vh;
      right: 150px;
      left: unset; }

.q3 .animate-section__circle {
      width: 250px;
      height: 250px;
      border-radius: 50%;
      position: absolute;
      top: 120vh;
      right: -50px;
      background: white; }

.q3 .animate-section__tri {
      width: 0;
      height: 0;
      border-color: transparent transparent black transparent;
      border-width: 0px 90px 160px 90px;
      position: absolute;
      top: 100vh;
      right: 0px; }

.calculating {
  background: #1469FF;
  width: 100vw;
  height: 5000vh; }

.calculating .small-circle {
    border-radius: 50%; }

.calculating .calculate-text {
    color: white;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center; }

.calculating .calculate-text__title {
      font-size: 64px;
      font-weight: 700; }

.calculating .calculate-text__desc {
      font-size: 24px; }
